// Galiovets
.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;

  // @media screen and (min-width: 320px) {
  //   width: 320px;
  // }

  @media screen and (min-width: 480px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-right: 34px;
    padding-left: 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-right: 115px;
    padding-left: 115px;
  }
}
