.backdrop {
  position: fixed;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.2);

  top: 0;
  left: 0;

  z-index: 999;

  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &--is-hidden {
    opacity: 0;
    pointer-events: none;
  }
}

.modal {
  position: absolute;
  max-width: 300px;
  max-height: 100%;

  background-color: $modal-bg-color;

  border-radius: 4px;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 480px) {
    max-width: 430px;
  }

  @media screen and (min-width: 1200px) {
    max-width: 528px;
  }
}

.backdrop--is-hidden .modal {
  transform: translate(-50%, -50%) scale(1.1);
}
.form {
  display: flex;
  flex-direction: column;
  padding: 0 40px;
}

.form__label {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.form__label-name {
  position: absolute;
  top: -5px;
  left: -25px;

  transform: translateY(-15px);
  font-size: 15px;
  line-height: 1.2;
  display: flex;
  align-items: center;
  text-align: center;

  color: $accent-color;

  @media screen and (min-width: 480px) {
    top: -5px;
    left: -5px;
  }
  @media screen and (min-width: 1200px) {
    top: -5px;
    left: 2px;
  }
}

.form__tittle--bold {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $accent-color;

  margin-top: 40px;
  margin-bottom: 30px;

  @media screen and (max-width: 1199px) {
    line-height: 1.5;
  }
}

.form__input {
  width: 280px;
  height: 40px;
  border: 1px solid (rgba(33, 33, 33, 0.2));
  border-radius: 4px;
  background-color: $modal-input-color;

  padding-left: 10px;

  margin-bottom: 28px;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 480px) {
    width: 370px;
  }

  @media screen and (min-width: 1200px) {
    width: 448px;
  }
}

.form__label:focus-within > .form__input,
.form__label:focus-within > .form__textarea {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
  stroke: $main-color;
  outline: none;
  border-color: $main-color;
}

.form__textarea {
  width: 280px;
  resize: none;
  height: 120px;
  cursor: pointer;
  background-color: $modal-input-color;

  padding-left: 12px;
  padding-top: 8px;

  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  margin-bottom: 20px;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 480px) {
    width: 370px;
  }
  @media screen and (min-width: 1200px) {
    width: 448px;
  }

  &::placeholder {
    @media screen and (max-width: 1199px) {
      font-size: 14px;
      line-height: 1.14;
      letter-spacing: 0.01em;
    }

    color: rgba(0, 0, 0, 0.2);
    font-size: 16px;
    line-height: 1.25;
  }
}

.form__submit-button {
  width: 100px;
  margin: 10px 32px;
  padding: 10px 32px;
  border: transparent;
  background-color: $accent-color;
  border-radius: 20px;
  color: $main-color;

  margin-left: auto;
  margin-right: auto;

  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.1;
  cursor: pointer;

  &:hover,
  &:focus {
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }
  @media screen and (min-width: 1200px) {
    width: 120px;
  }
}

.form__close-button {
  position: absolute;
  right: 7px;
  top: 7px;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 30px;
  height: 30px;

  stroke: $main-text-color;
  background-color: $modal-input-color;
  border: none;
  border-radius: 50px;

  cursor: pointer;

  &:hover,
  &:focus {
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }
}

.form__label-select {
  cursor: pointer;
  display: inline-block;
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  color: #000;
  line-height: 1.3;
  padding-left: 5px;
  width: 220px;
  height: 40px;
  border: 1px solid (rgba(33, 33, 33, 0.2));
  border-radius: 4px;
  background-color: $modal-input-color;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  margin-bottom: 35px;

  @media screen and (min-width: 480px) {
    width: 350px;
  }

  @media screen and (min-width: 1200px) {
    width: 448px;
  }
}
